<template>

  <TaskCard less="4" title="Сервис новостей" v-bind:tech="['JavaScript']" level="Средний" v-if="view === 'card'"/>

  <ul v-if="view === 'note'">
    <Link to="https://learn.javascript.ru/modifying-document"><img src="@/assets/jslearn.png" height="24" alt="">Изменение документа</Link>
    <Link to="https://learn.javascript.ru/fetch"><img src="@/assets/jslearn.png" height="24" alt="">Fetch</Link>
    <Link to="https://learn.javascript.ru/object"><img src="@/assets/jslearn.png" height="24" alt="">Объекты</Link>
  </ul>

  <div class="do" v-if="view === 'do'">
    <span>Вам следует разработать сервис, который будет выводить актуальный список новостей<br>Функционал:</span>
    <ul>
      <li>Все новости должны получаться из любого API</li>
      <li>Выводиться должно только 10 новостей </li>
      <li>Карточка с новостью, должна иметь дату, автора, заголовок</li>
      <li>Новость должна открываться в новой вкладке</li>
    </ul>
    <span><br>В демо использовался <a target="_blank" href="https://newsapi.org/">newsapi.org</a></span>
  </div>

  <div class="container task_view" v-if="view === 'task'">
      <a class="new_card" v-for="newItem in news" :key="newItem.title" :href="newItem.url" target="_blank">
        <span class="new_card__title">{{newItem.title}}</span>
        <div class="new_card__meta">
          <span class="new_card__meta_author">{{newItem.author}}</span>
          <span class="new_card__meta_date">{{formatDate(newItem.publishedAt)}}</span>
        </div>
      </a>
  </div>
</template>

<script>
import Link from "@/components/Link";
import TaskCard from "@/components/TaskCard";


export default {
  name: "NewsT",
  components : {
    Link,
    TaskCard
  },
  props : {
    view : {
      type : String
    }
  },
  data(){
    return {
      news : []
    }
  },
  methods : {
    fNum(n){
      if(n < 10) return `0${n}`;
      return n;
    },
    formatDate(date){
      let d = new Date(date);

      return `${this.fNum(d.getDate())}.${this.fNum(d.getMonth()+1)}.${d.getFullYear()}`;
    }
  },
  async mounted() {
    if (this.view === "task") {
      let res = await fetch("https://newsapi.org/v2/top-headlines?country=ru&apiKey=33a4bd28f02e42dc82f5ef9430ae6e58");
      if (res.ok) {
        let json = await res.json();
        console.log(json)
        this.news = json.articles.slice(10,json.articles.length);
        console.log(this.news)
      }
    }
  }
}
</script>

<style scoped lang="scss">
@import "@/assets/styles.scss";
.task_view{
  padding: 7vh 0;
  color: #fff;
}

.new_card{
  background: $second-color;
  padding: 10px;
  margin: 1em 0;
  display: flex;
  flex-direction: column;
  text-decoration: none;
  color: #fff;
  transition: all .3s linear 0s;
  &:hover{
    transform: scale(1.03);
  }
}
.new_card__meta{
  display: flex;
  align-items: center;
  color: $color-hint;

}
.new_card__meta_date{
  margin-left: 1vw;
}
.new_card__title{
  font-size: 1.3em;
  font-weight: 600;
}
</style>